import React, { Component } from "react";
import { connect } from "react-redux";
import {
  SchoolData,
  Auth,
  Basic,
  FC_GetSchoolsByDistrict,
  FC_GetAllPositions,
  FC_GetSchoolPositions,
  FC_AllSchoolHierarchyDetails,
  SchoolHierarchyStore,
} from "../../actions";
import { StoreState } from "../../reducers";

export class _RegisterSystemUsersList extends Component {
  render() {
    return <div>RegisterSystemUsersList</div>;
  }
}

const mapStateToProps = ({
  auth,
  basic,
  schoolHierarchyStore,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  schoolHierarchyStore: SchoolHierarchyStore;
} => {
  return {
    auth: auth,
    basic: basic,
    schoolHierarchyStore: schoolHierarchyStore,
  };
};

export const RegisterSystemUsersList = connect(mapStateToProps, {
  FC_GetAllPositions,
  FC_AllSchoolHierarchyDetails,
})(_RegisterSystemUsersList);
